<template >
  <div class="">
    <!-- <div class="model" v-show="model" @click="model = false">
      <div class="model-show">
        <img :src="modelSrc" alt="">
      </div>
    </div> -->
    <div class="d-flex gap-1 justify-content-center align-items-center">
      <Loading :active="isLoading"></Loading>

      <div class="cut">
        <vue-cropper ref="cropper" :img="option.img" :output-size="option.size" :output-type="option.outputType"
          :info="true" :full="option.full" :fixed="fixed" :fixed-number="fixedNumber" :can-move="option.canMove"
          :can-move-box="option.canMoveBox" :fixed-box="option.fixedBox" :original="option.original"
          :auto-crop="option.autoCrop" :auto-crop-width="option.autoCropWidth" :auto-crop-height="option.autoCropHeight"
          :center-box="option.centerBox" @real-time="realTime" :high="option.high" @img-load="imgLoad" mode="contain"
          :max-img-size="option.max" @crop-moving="cropMoving"></vue-cropper>
      </div>
      <div class="show-preview"
        :style="{ 'width': previews.w + 'px', 'height': previews.h + 'px', 'overflow': 'hidden', 'margin': '5px' }">
        <div :style="previews.div">
          <img :src="previews.url" :style="previews.img">
        </div>
      </div>
    </div>
    <!--  -->
    <div class="test-button">
      <!-- <button @click="changeImg" class="btn_2">changeImg</button> -->
      <label class="btn_2" for="uploads">upload</label>
      <input type="file" id="uploads" style="position:absolute; clip:rect(0 0 0 0);"
        accept="image/png, image/jpeg, image/gif, image/jpg" @change="uploadImg($event, 1)">
      <!-- <button @click="startCrop" v-if="!crap" class="btn_2">start</button> -->
      <!-- <button @click="stopCrop" v-else class="btn_2">stop</button> -->
      <button @click="clearCrop" class="btn_2">clear</button>
      <!-- <button @click="refreshCrop" class="btn_2">refresh</button> -->
      <button @click="changeScale(1)" class="btn_2">+</button>
      <button @click="changeScale(-1)" class="btn_2">-</button>
      <button @click="rotateLeft" class="btn_2">rotateLeft</button>
      <button @click="rotateRight" class="btn_2">rotateRight</button>
      <!-- <button @click="finish('base64')" class="btn_2">preview(base64)</button> -->
      <!-- <button @click="finish('blob')" class="btn_2">preview(blob)</button> -->
      <a @click="down('base64')" class="btn_2">download(base64)</a>
      <a @click="down('blob')" class="btn_2">download(blob)</a>
      <!-- <a @click="() => option.img = ''" class="btn_2"></a> -->
      <div style="display:block; width: 100%;">
        <div class="m-5 d-grid gap-3">
          <button type="button" name="" id="" class="btn btn-danger w-100" @click="finish('blob')">confirm</button>
          <button type="button" name="" id="" class="btn btn-secondary w-100"
            @click.stop="$emit('close', openModal)">close</button>
        </div>
        <!--  -->
        <div class="" v-if="false">
          <label class="c-item">
            <span> ( )</span>
            <input type="checkbox" v-model="option.original">
            <span>original: {{ option.original }}</span>
          </label>
          <label class="c-item">
            <span></span>
            <input type="checkbox" v-model="option.canMove">
          </label>
          <label class="c-item">
            <span></span>
            <input type="checkbox" v-model="option.canMoveBox">
            <span>canMoveBox: {{ option.canMoveBox }}</span>
          </label>
          <label class="c-item">
            <span></span>
            <input type="checkbox" v-model="option.fixedBox">
            <span>fixedBox: {{ option.fixedBox }}</span>
          </label>
          <label class="c-item">
            <span></span>
            <input type="checkbox" v-model="option.full">
            <span>full: {{ option.full }}</span>
          </label>
          <label class="c-item">
            <span></span>
            <input type="checkbox" v-model="option.autoCrop">
            <span>autoCrop: {{ option.autoCrop }}</span>
          </label>
          <label class="c-item">
            <span>dpr</span>
            <input type="checkbox" v-model="option.high">
            <span>high: {{ option.high }}</span>
          </label>
          <label class="c-item">
            <span>()</span>
            <input type="checkbox" v-model="option.centerBox">
            <span>centerBox: {{ option.centerBox }}</span>
          </label>
          <label class="c-item">
            <p></p>
            <label>jpg
              <input type="radio" name="type" value="jpeg" v-model="option.outputType">
            </label>
            <label>png
              <input type="radio" name="type" value="png" v-model="option.outputType">
            </label>
            <label>webp
              <input type="radio" name="type" value="webp" v-model="option.outputType">
            </label>
          </label>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { VueCropper } from 'vue-cropper';
import 'vue-cropper/dist/index.css';
// import modalMixin from '@/mixins/modalMixin';

export default {
  // mixins: [modalMixin],
  components: { VueCropper },
  data () {
    return {
      model: false,
      modelSrc: '',
      crap: false,
      previews: {},
      lists: [
        {
          img: 'https://avatars2.githubusercontent.com/u/15681693?s=460&v=4'
        }
      ],
      option: {
        img: '', // 
        // img: 'https://storage.googleapis.com/vue-course-api.appspot.com/william-api/1690362062055.jpg?GoogleAccessId=firebase-adminsdk-zzty7%40vue-course-api.iam.gserviceaccount.com&Expires=1742169600&Signature=DuRa%2FaTvXVYlqq5DlHjjAzhQ95PcMs3IuJA0iqm0Ma1%2BcEMeyFvmsh3%2FwxGSpe8Z11D32ZlQbr8i8czcj3HRgeWpCxkuNzBYQJhJAJZ40X%2FLm81ddIkktUtRWf7Dqz1UQtZnyItnklxeKqM4%2Bzj2Bc5kujHowfPz%2BZeo95i6fH46ZYv1L6FHVXVm5AsxP4UBYUQagPCNemrsCTA9md9Aj21r%2BKUtIlvE9GnqhNp2U8QPZLV%2BWzuZnC9DTgfI%2B%2Fv9wQipEf0fhMVWb%2FYs%2BWc5PQOKfenkNPTDOOb3cVg8ALRxo27QADbfkfpZhIwDoKHrWH3BkxDFeclL4B1OYVWXlw%3D%3D',
        // img: this.tempImg,
        size: 1,
        full: false,
        outputType: 'png',
        canMove: true,
        fixedBox: false,
        original: false,
        canMoveBox: true,
        autoCrop: true,
        //   /  .cut 
        autoCropWidth: 350,
        autoCropHeight: 340,
        centerBox: true,
        high: true,
        max: 99999
      },
      show: true,
      fixed: false,
      fixedNumber: [75, 34],
      //
      isLoading: false
    };
  },
  props: {
    tempImg: { //! tempProduct.imageUrl >  > 
      type: Object,
      default () {
        return {};
      }
    }
  },
  created () {
    // console.log(this.tempImg);
  },
  watch: {
    // tempImg () { //! props
    // console.log('tempImg', this.tempImg.url);
    // this.option.img = this.tempImg.url;
    //
    // const imageUrl = this.tempImg.url;
    //   uploadImg() finish()base64
    // const imageUrl = 'https://storage.googleapis.com/vue-course-api.appspot.com/william-api/1690362062055.jpg';
    // const imageUrl = this.tempImg.url;
    // fetch(imageUrl)// Failed to fetch
    //   .then(response => response.blob())
    //   .then(blob => {
    //     const reader = new FileReader();
    //     reader.onloadend = () => {
    //       const base64data = reader.result;
    //       this.option.img = base64data;
    //       console.log('base64 data', base64data);
    //     // Use the base64 data here
    //     };
    //     reader.readAsDataURL(blob);
    //   });
    // 
    // this.convertImgLinkToBlob(imageUrl).then((blob) => {
    //   // blob
    //   // blob
    //   console.log(blob);// null
    // });
    //   > CORSAPI > vue.config.js 
    // this.downloadAndUploadImage(imageUrl); //Network Error
    // }
  },
  methods: {
    // ? 
    convertImgLinkToBlob (imageUrl) {
      return new Promise((resolve) => {
        const xhr = new XMLHttpRequest();
        xhr.open('GET', imageUrl, true);
        xhr.responseType = 'blob';
        xhr.onload = function () {
          if (xhr.status === 200) {
            resolve(xhr.response);
          } else {
            resolve(null);
          }
        };
        xhr.send();
      });
    },
    downloadAndUploadImage (url) {
      this.axios({
        url,
        responseType: 'arraybuffer'
      })
        .then((response) => {
          const imageBuffer = Buffer.from(response.data, 'binary');
          // Vue Cropper
          console.log(imageBuffer);
          // URL
          // const uploadedImageUrl = 'https://example.com/uploaded-image.jpg';
          // uploadedImageUrlimg2
          // this.img2 = uploadedImageUrl;
          // Vue Cropperimg2
        })
        .catch((error) => {
          console.error('Error:', error);
        });
    },
    changeImg () {
      this.option.img = this.lists[~~(Math.random() * this.lists.length)].img;
    },
    startCrop () {
      // start
      this.crap = true;
      this.$refs.cropper.startCrop();
    },
    stopCrop () {
      //  stop
      this.crap = false;
      this.$refs.cropper.stopCrop();
    },
    clearCrop () {
      // clear
      this.$refs.cropper.clearCrop();
    },
    refreshCrop () {
      // clear
      this.$refs.cropper.refresh();
    },
    changeScale (num) {
      num = num || 1;
      this.$refs.cropper.changeScale(num);
    },
    rotateLeft () {
      this.$refs.cropper.rotateLeft();
    },
    rotateRight () {
      this.$refs.cropper.rotateRight();
    },
    // ?base64
    finish (type) {
      this.isLoading = true;
      // 
      // var test = window.open('about:blank')
      // test.document.body.innerHTML = '..'
      if (type === 'blob') {
        // console.log(this.$refs.cropper);
        this.$refs.cropper.getCropBlob((data) => {
          // console.log(data);// blob
          const newImg = new File([data], 'croppered.jpg');
          // console.log(newImg);// file
          //  
          // formdata
          const formData = new FormData();//! 
          formData.append('file-to-upload', newImg);
          const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/upload`;
          this.$http.post(url, formData).then((res) => {
            if (res.data.success) {
              const finishCropper = this.tempImg;
              finishCropper.url = res.data.imageUrl;
              this.$emit('confirm-cropper', finishCropper);
              this.isLoading = false;
            }
          });
          //
          // const img = window.URL.createObjectURL(data);
          // this.model = true;
          // this.modelSrc = img;
        });
      } else {
        this.$refs.cropper.getCropData((data) => {
          this.model = true;
          this.modelSrc = data;
        });
      }
    },
    // 
    realTime (data) {
      this.previews = data;
      // console.log(data);
    },
    finish2 (type) {
      this.$refs.cropper2.getCropData((data) => {
        this.model = true;
        this.modelSrc = data;
      });
    },
    finish3 (type) {
      this.$refs.cropper3.getCropData((data) => {
        this.model = true;
        this.modelSrc = data;
      });
    },
    down (type) {
      // event.preventDefault()
      const aLink = document.createElement('a');
      aLink.download = 'demo';
      // 
      if (type === 'blob') {
        this.$refs.cropper.getCropBlob((data) => {
          this.downImg = window.URL.createObjectURL(data);
          aLink.href = window.URL.createObjectURL(data);
          aLink.click();
        });
      } else {
        this.$refs.cropper.getCropData((data) => {
          this.downImg = data;
          aLink.href = data;
          aLink.click();
        });
      }
    },
    // ?  >  > 
    uploadImg (e, num) {
      // 
      // this.option.img
      const file = e.target.files[0];
      // console.log('file', typeof (file), file);
      //
      if (!/\.(gif|jpg|jpeg|png|bmp|GIF|JPG|PNG)$/.test(e.target.value)) {
        alert('.gif,jpeg,jpg,png,bmp');
        return false;
      }
      const reader = new FileReader();
      reader.onload = (e) => {
        let data;
        if (typeof e.target.result === 'object') {
          // Array Bufferblob base64
          data = window.URL.createObjectURL(new Blob([e.target.result]));
        } else {
          data = e.target.result;
        }
        if (num === 1) {
          this.option.img = data;
        } else if (num === 2) {
          this.example2.img = data;
        }
      };
      // base64
      // reader.readAsDataURL(file)
      // blob
      reader.readAsArrayBuffer(file);
    },
    imgLoad (msg) {
      // console.log(msg);
    },
    cropMoving (data) {
      // console.log(data, '');
    }
  },
  mounted: function () {
    // console.log(window['vue-cropper']);
  }
};
</script>
<style lang="css" scoped>
* {
  margin: 0;
  padding: 0;
}

.cut {
  width: 500px;
  height: 500px;
  margin: 30px;
}

.c-item {
  max-width: 800px;
  margin: 10px auto;
  margin-top: 20px;
}

.content {
  margin: auto;
  max-width: 1200px;
  margin-bottom: 100px;
}

.test-button {
  display: flex;
  flex-wrap: wrap;
  align-content: center;
  justify-content: center;
}

.btn_2 {
  display: inline-block;
  line-height: 1;
  white-space: nowrap;
  cursor: pointer;
  background: #fff;
  border: 1px solid #c0ccda;
  color: #1f2d3d;
  text-align: center;
  box-sizing: border-box;
  outline: none;
  margin: 20px 10px 0px 0px;
  padding: 9px 15px;
  font-size: 14px;
  border-radius: 4px;
  color: #fff;
  background-color: #50bfff;
  border-color: #50bfff;
  transition: all .2s ease;
  text-decoration: none;
  user-select: none;
}

.des {
  line-height: 30px;
}

code.language-html {
  padding: 10px 20px;
  margin: 10px 0px;
  display: block;
  background-color: #333;
  color: #fff;
  overflow-x: auto;
  font-family: Consolas, Monaco, Droid, Sans, Mono, Source, Code, Pro, Menlo, Lucida, Sans, Type, Writer, Ubuntu, Mono;
  border-radius: 5px;
  white-space: pre;
}

.show-info {
  margin-bottom: 50px;
}

.show-info h2 {
  line-height: 50px;
}

/*.title, .title:hover, .title-focus, .title:visited {
        color: black;
      }*/

.title {
  display: block;
  text-decoration: none;
  text-align: center;
  line-height: 1.5;
  margin: 20px 0px;
  background-image: -webkit-linear-gradient(left, #3498db, #f47920 10%, #d71345 20%, #f7acbc 30%, #ffd400 40%, #3498db 50%, #f47920 60%, #d71345 70%, #f7acbc 80%, #ffd400 90%, #3498db);
  color: transparent;
  -webkit-background-clip: text;
  background-size: 200% 100%;
  animation: slide 5s infinite linear;
  font-size: 40px;
}

.test {
  height: 300px;
}

.model {
  position: fixed;
  z-index: 10;
  width: 100vw;
  max-height: 50vh;
  overflow: auto;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.8);
}

.model-show {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
}

.model img {
  display: block;
  margin: auto;
  max-width: 80%;
  user-select: none;
  background-position: 0px 0px, 10px 10px;
  background-size: 20px 20px;
  background-image: linear-gradient(45deg, #eee 25%, transparent 25%, transparent 75%, #eee 75%, #eee 100%), linear-gradient(45deg, #eee 25%, white 25%, white 75%, #eee 75%, #eee 100%);
}

.c-item {
  display: block;
  user-select: none;
}

@keyframes slide {
  0% {
    background-position: 0 0;
  }

  100% {
    background-position: -100% 0;
  }
}
</style>
