<template>
  <div class="w-100 ">
    <Loading :active="isLoading"></Loading>
    <div class="text-end mt-3">
      <button class="btn btn-primary" type="button" @click="openModal(true)">
        
      </button>
    </div>
    <table class="table mt-4">
      <thead>
        <tr>
          <th class="d-md-table-cell d-none" width="120"></th>
          <th></th>
          <th class="d-md-table-cell d-none" width="120"></th>
          <th class="d-md-table-cell d-none" width="120"></th>
          <th class="d-md-table-cell d-none" width="100"></th>
          <th width="150"></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="item in products" :key="item.id">
          <td class="d-md-table-cell d-none">{{ item.category }}</td>
          <td>{{ item.title }}</td>
          <td class="text-right d-md-table-cell d-none">
            <!-- methods/filters -->
            {{ $filters.currency(item.origin_price) }}
          </td>
          <td class="text-right d-md-table-cell d-none">
            {{ $filters.currency(item.price) }} </td>
          <td class="d-md-table-cell d-none">
            <span class="text-success" v-if="item.is_enabled"></span>
            <span class="text-muted" v-else></span>
          </td>
          <td class="text-center">
            <div class="btn-group ">
              <button class="btn btn-outline-primary btn-sm" @click="openModal(false, item)"></button>
              <button class="btn btn-outline-danger btn-sm" @click="openDelProductModal(item)"></button>
            </div>
          </td>
        </tr>
      </tbody>
    </table>
    <!-- props emit -->
    <Pagination :pages="pagination" @emit-pages="getProducts"></Pagination>
    <!-- emit -->
    <ProductModal ref="productModal" :product="tempProduct" @update-product="updateProduct"></ProductModal>
    <!--  -->
    <DelModal :item="tempProduct" ref="delModal" @del-item="delProduct" />
  </div>
</template>
<script>
import ProductModal from '@/components/ProductModal.vue';
import Pagination from '@/components/Pagination.vue';
import DelModal from '@/components/DelModal.vue';
export default {
  components: {
    ProductModal,
    DelModal,
    Pagination
  },
  inject: ['emitter'],
  data () {
    return {
      products: [], //* 
      pagination: {}, //* 
      tempProduct: {}, //* 
      isNew: false, //* 
      isLoading: false, //* 
      temp_current_page: 1 //* 
    };
  },
  created () {
    this.getProducts();
  },
  methods: {
    //  
    // !
    getProducts (page = this.temp_current_page) {
      this.temp_current_page = page; //* nullres
      const api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/products/?page=${this.temp_current_page}`;
      this.isLoading = true;
      this.$http.get(api).then((res) => {
        this.isLoading = false;
        if (res.data.success) {
          this.products = res.data.products;
          this.pagination = res.data.pagination;
        }
      });
    },
    //! truefalse
    openModal (isNew, item) {
      if (isNew) {
        this.tempProduct = {};
      } else {
        this.tempProduct = { ...item };
      }
      this.isNew = isNew;
      const productComponent = this.$refs.productModal;
      productComponent.showModal();
    },
    //* 
    updateProduct (item) {
      this.tempProduct = item;
      // 
      let api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/product`;
      let httpMethod = 'post';
      // 
      if (!this.isNew) {
        api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/product/${item.id}`;
        httpMethod = 'put';
      }
      const productComponent = this.$refs.productModal;
      this.$http[httpMethod](api, { data: this.tempProduct }).then((response) => {
        productComponent.hideModal();
        if (response.data.success) {
          this.getProducts();
          // ! mitt 
          // this.emitter.emit('push-message', {
          //   style: 'success',
          //   title: ''
          // });
          this.$httpMessageState(response, '');
        } else {
          // this.emitter.emit('push-message', {
          //   style: 'danger',
          //   title: '',
          //   content: response.data.message.join('')
          // });
          this.$httpMessageState(response, '');
        }
      });
    },
    //  Modal
    openDelProductModal (item) {
      this.tempProduct = { ...item };
      const delComponent = this.$refs.delModal;
      delComponent.showModal();
    },
    delProduct () {
      // !
      const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/product/${this.tempProduct.id}`;
      this.$http.delete(url).then((response) => {
        // console.log(response.data.success);
        this.$httpMessageState(response, response.data.message);
        const delComponent = this.$refs.delModal;
        delComponent.hideModal();
        this.getProducts();
      });
    }
  }
};
</script>
<style lang="scss" scoped>
tr:nth-child(even) {
  background-color: #eee;
}

tr:hover {
  background-color: rgba(0, 81, 255, 0.237)
}
</style>
